.section-info {
	background-color: rgba(88, 66, 154, 0.15);
	color: $bcls__purple;
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;

	&::after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1;
		background-image: url(https://raw.githubusercontent.com/biaoliveira09/bclandsummit/1ec5180edc7a6afe13e8880615c2dea45dface73/assets/qqquad.png);
		opacity: 0.4;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
	}

	.info-text {
		padding: 2rem;
		margin-right: 1rem;

		h2 {
			font-family: 'Poppins', sans-serif;
			font-weight: 300;
			font-size: 0.85rem;
			text-transform: uppercase;
			position: relative;
			display: inline-block;
			letter-spacing: 1px;
			text-align: center;
		}

		h3 {
			font-size: 1.5rem;
		}

		.info-section-text {
			position: relative;

			&:before {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				top: -30px;
				width: 100%;
				border-top: 1px solid $bcls__purple;
			}
		}
	}

	.venue-pics {
		display: flex;
		flex-wrap: wrap;

		img {
			width: 100%;
		}
	}
}

@media screen and (min-width: 55em) {
	.section-info {
		flex-direction: row;

		.info-container {
			display: flex;
			justify-content: space-between;
			align-items: center;

			.venue-pics {
				max-width: 30%;
			}
			.info-text {
				margin: 0 auto;
				max-width: 570px;

				h2 {
					display: inline-block;
					letter-spacing: 1px;
					text-align: center;
				}

				h3 {
					font-size: 1.5rem;
				}
			}
		}
	}
}
